@import url(https://fonts.googleapis.com/css2?family=Cantarell&display=swap);
:root {
  --light: #f0f0f0;
  --primary: #404040;
  --secondary: #bfbfbf;
}

.App {
}

:root {
  --spacingIcon: 50px;
}
.styleNavbar ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 10px 15px;
  border-radius: 10px 10px 0px 0px;
  position: relative;
}
b {
  width: 50px;
  height: 50px;
}
b::before {
  position: absolute;
  width: 50px;
  height: 50px;
  content: "";
  background: var(--primary);
  border-radius: 0px 0px 10px 0px;
}
c {
  width: 50px;
  height: 50px;
}
c::before {
  position: absolute;
  width: 50px;
  height: 50px;
  content: "";
  background: var(--primary);
  border-radius: 0px 0px 0px 10px;
}

